import { Form } from "antd";
import { useState } from "react";
import { Fn } from "../../helper/function";
import { AuthService } from "../../services/authService";
import { MyFormItem } from "../core/Input";
import { MyBox } from "../core/MyBox";
import { MyButton } from "../core/MyButton";

export default function ForgotPasswordComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [userName, setUserName] = useState('');
    const [otp, setOtp] = useState('');

    function handleClickBtnSendOTP() {
        setIsLoading(true);
        let body = {
            userName: userName
        };

        AuthService.RequestForgotPassword(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }

    function handleClickBtnResetPassword() {
        setIsLoading(true);
        let body = {
            userName: userName,
            otp: otp
        };

        AuthService.VerifyForgotPassword(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox title="Quên mật khẩu" >
            <Form layout="vertical">
                <MyFormItem.Input
                    label="Tên đăng nhập"
                    value={userName}
                    setValue={setUserName} />
                <MyButton
                    value="Gửi mã OTP"
                    isLoading={isLoading}
                    onClick={handleClickBtnSendOTP} />
                <MyFormItem.Password
                    label="OTP"
                    value={otp}
                    setValue={setOtp} />

                <MyButton
                    value="Đổi mật khẩu"
                    isLoading={isLoading}
                    onClick={handleClickBtnResetPassword} />
            </Form>
        </MyBox>
    )
}